import * as React from 'react';
import {useEffect, useState} from 'react';
import ProductHero from './modules/views/ProductHero';
import ProductValues from './modules/views/ProductValues';
import AppAppBar from './modules/views/AppAppBar';
import withRoot from './modules/withRoot';
import OutRules from './modules/views/Rules/OutRules';
import {Link, useLocation} from 'react-router-dom';
import {ToggleButtonGroup, ToggleButton, Box} from '@mui/material';


function Index() {

    const category = [
        <ToggleButton value="welcom" LinkComponent={Link} to="/" replace><h3>입실</h3></ToggleButton>,
        <ToggleButton value="jacuzzi" LinkComponent={Link} to="/Jaccuzi" replace><h3>자쿠지</h3></ToggleButton>,
        <ToggleButton value="bbq" LinkComponent={Link} to="/BBQ" replace><h3>BBQ</h3></ToggleButton>,
        <ToggleButton value="warning" LinkComponent={Link} to="/Warning" style={{color:'red'}} replace><h3>긴급 연락망</h3></ToggleButton>,
        <ToggleButton value="out" LinkComponent={Link} to="/OutRules" replace><h3>퇴실</h3></ToggleButton>
    ]

    const [alignment, setAlignment] = useState('');

    const handleChange = (event, newAlignment) => {
        if (newAlignment !== null) {
            setAlignment(newAlignment);
        }
    };

    return (
        <React.Fragment>
            <AppAppBar/>
            <ProductHero/>
            <Box justifyContent='center' sx={{backgroundColor: '#fffaf0', pt: 3, pb: 1}}>
                <Box textAlign='center' justifyContent='center' sx={{pt: 2, ml: 2, mr: 2}}>
                    <ToggleButtonGroup size="small" fullWidth={true} exclusive color="primary" value={alignment}
                                       onChange={handleChange} aria-label="Medium sizes">
                        {category}
                    </ToggleButtonGroup>
                </Box>
                <ProductValues/>
            </Box>
        </React.Fragment>
    );
}

export default withRoot(Index);
